import React from 'react';
import './App.css';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import MainWindow from "./MainWindow";
import OrientationWindow from "./OrientationWindow";

function App() {
  return (
      <Router>
          <Switch>
              <Route path="/cowboy/game">
                  <MainWindow />
              </Route>
              <Route path="/cowboy/orientation">
                  <OrientationWindow />
              </Route>
              <Route path="/cowboy/won">
                  <div className="space">
                      <h1>You won!</h1>
                      <Link className='button' to="/cowboy/game">Play again</Link>
                  </div>

              </Route>
              <Route path="/cowboy/lost">
                  <div className="space">
                      <h1>You lost!</h1>
                      <Link className='button' to="/cowboy/game">Play again</Link>
                  </div>
              </Route>
              <Route path="/cowboy">
                  <div className="space">
                      <Link className='button' to="/cowboy/game">Game</Link>
                      <Link className='button' to="/cowboy/orientation">Tests</Link>
                  </div>
              </Route>

          </Switch>
    </Router>
  );
}

export default App;
