import React from "react";
import './App.css';
import './OrientationWindow.css';

class OrientationWindow extends React.Component {
    state = {
        alpha: 0,
        beta: 0,
        gamma: 0,
        x: 0,
        y: 0,
        z: 0,
    }

    getPermission = () => {
        if(typeof( DeviceOrientationEvent ) !== "undefined" ) {
            if (typeof (DeviceOrientationEvent.requestPermission) === "function") {
                DeviceOrientationEvent.requestPermission()
                    .then(response => {
                        // (optional) Do something after API prompt dismissed.
                        alert("Orientation resp " + response);
                        if (response === "granted") {
                            window.addEventListener("deviceorientation", (event) => {
                                this.setState({
                                    alpha: event.alpha,
                                    beta: event.beta,
                                    gamma: event.gamma
                                })
                            })
                        }
                    });
            }
            window.addEventListener("deviceorientation", (event) => {
                this.setState({
                    alpha: event.alpha,
                    beta: event.beta,
                    gamma: event.gamma
                })
            })
        }
        if(typeof( DeviceMotionEvent ) !== "undefined" ) {
            if(typeof( DeviceMotionEvent.requestPermission ) === "function") {
                DeviceMotionEvent.requestPermission()
                    .then(response => {
                        // (optional) Do something after API prompt dismissed.
                        alert("Motion resp " + response);
                        if (response === "granted") {
                            window.addEventListener("devicemotion", (event) => {
                                this.setState({
                                    x: event.acceleration.x,
                                    y: event.acceleration.y,
                                    z: event.acceleration.z
                                })
                            });
                        }
                    });
                window.addEventListener("devicemotion", (event) => {
                    this.setState({
                        x: event.acceleration.x,
                        y: event.acceleration.y,
                        z: event.acceleration.z
                    })
                });
            }
        }
    }


    render() {
        return(
            <div className="space">
                <h5>x,y,z are acceleration variables.</h5>
                <h5>Alpha, beta and gamma are deltas between device axes and coordinate axes</h5>
                {Object.keys(this.state).map((key)  =>
                <div className="data-wrapper">
                    <span>{key}</span>
                    <span>{this.state[key].toFixed(2)}</span>
                </div>
                )}
                <button onClick={this.getPermission}>Press me</button>
            </div>
        )
    }
}

export default OrientationWindow;