import React from "react";
import './App.css';
import './MainWindow.css';
import {Redirect} from "react-router-dom";
import weapon from'./images/weapon.png';
import startSound from "./audio/start.mp3";
import countSound from './audio/countSound.mp3';
import shotEnemy from './audio/shotEnemy.mp3';
import shotPlayer from './audio/shotPlayer.mp3';
import fireSound from './audio/fireSound.mp3';

/*
TODO: Fix falschstart, add death sound, add endgame sound, check shoot speed on android, reverse gun.
 */

class MainWindow extends React.Component {
    state = {
        isChecked: false,
        alpha: 0,
        beta: 0,
        gamma: 0,
        isDown: false,
        isCounting: false,
        counted: false,
        enemyShot: false,
        playerShot: false,
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(this.state.isChecked && !prevState.isChecked) {
            this.startAudio.play();
            window.addEventListener("deviceorientation", this.phoneDownEventListener);
        }

        if (!this.state.isCounting && this.state.isDown && !this.state.counted) {
            this.startAudio.pause();
            this.startAudio.currentTime = 0;
            this.countAudio.onended = () => {
                this.fireSoundAudio.currentTime = 0.2
                this.fireSoundAudio.play();
                this.setState({counted: true});
            }
            this.countAudio.play();
            this.setState({isCounting: true});
        }

        if(this.state.isCounting && !this.state.isDown && !this.state.counted) {
            this.countAudio.pause();
            this.countAudio.currentTime = 0;
            this.startAudio.play();
            this.setState({isCounting: false});
        }

        if(this.state.counted && !prevState.counted) {
            window.removeEventListener("deviceorientation", this.phoneDownEventListener);
            window.addEventListener("deviceorientation", this.phoneShootEventListener);
            setTimeout(() => {
                this.shotEnemyAudio.onended = () =>{
                    this.setState({enemyShot: true});
                }
                this.shotEnemyAudio.play();
            }, 1900);
        }
    }

    phoneDownEventListener = (event) => {
        if(event.beta < -60 && event.beta > -110 && !this.state.isDown) {
            this.setState({isDown: true});

        }
        else if((event.beta > -60 || event.beta < -110) &&this.state.isDown) {
            this.setState({isDown: false});
        }
    }

    phoneShootEventListener = (event) => {
        if(!this.state.playerShot && ((event.beta > 0 && event.beta < 10) || (event.beta < -170 && event.beta > -180))) {
            this.shotPlayerAudio.onended = () =>{
                this.setState({playerShot: true});
            }
            this.shotPlayerAudio.play();
        }
    }


    requestPermission = () => {
        if(typeof(DeviceOrientationEvent) !== "undefined"){
            if(typeof(DeviceOrientationEvent.requestPermission) === "function") {
                DeviceOrientationEvent.requestPermission()
                    .then(response => {
                        if (response !== "granted") {
                            alert('Sorry, you cannot play this game.');
                            return false;
                        }
                    });

            }
        }
        return true;
    }
    startGame = () => {
        if(this.requestPermission()) {
            this.startAudio = new Audio(startSound);
            this.startAudio.load();
            this.startAudio.volume = 1;
            this.countAudio = new Audio(countSound);
            this.countAudio.load();
            this.countAudio.volume = 1;
            this.shotEnemyAudio = new Audio(shotEnemy);
            this.shotEnemyAudio.load();
            this.shotEnemyAudio.volume = 1;
            this.fireSoundAudio = new Audio(fireSound);
            this.fireSoundAudio.load();
            this.fireSoundAudio.volume = 1;
            this.shotPlayerAudio = new Audio(shotPlayer);
            this.shotPlayerAudio.load();
            this.shotPlayerAudio.volume = 1;
            this.setState({isChecked: true});

        }
    }

    render() {
        if(this.state.enemyShot) {
            window.removeEventListener('deviceorientation', this.phoneShootEventListener);
            if(this.state.playerShot) {
                return <Redirect to='/cowboy/won'/>;
            }
            else {
                return <Redirect to='/cowboy/lost'/>;
            }
        }
        return (
            <div className="space">

                {this.state.isChecked?
                    <div>
                        <img src={weapon} alt="Weapon" className="weapon"/>
                    </div>
                :
                    <div>
                        <button className="start-button" onClick={this.startGame}>Press to start</button>
                    </div>
                }
            </div>
        )
    }
}

export default MainWindow;